<template>
  <div class="host rect in-1" :class="{ hand: hasMore }" @click="toggle">
    <div class="d-flex">
      <div class="expand big-3" v-html="dTitle" />
      <div v-if="hasMore">
        <i class="fas fa-info fa-fw" title="Click for more" />
      </div>
    </div>

    <div ref="accordion" class="collapse">
      <div class="collapse-body more py-4">
        <i class="fas fa-angle-right" />
        <div class="d-inline more">
          <span style="padding-left: 3px" v-html="dMore" />
        </div>
      </div>
    </div>
    <div class="hashs">
      <i class="fas fa-hashtag" /><span v-html="dSummary" />
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      shown: false
    };
  },
  props: {
    title: {
      type: String,
      default: ""
    },
    summary: {
      type: String,
      default: ""
    },
    more: {
      type: String,
      default: ""
    }
  },
  computed: {
    dTitle() {
      return this.replace(this.title);
    },
    dSummary() {
      return this.replace(this.summary);
    },
    dMore() {
      return this.replace(this.more);
    },
    hasMore() {
      return this.more.trim().length && true;
      // let s = this.$slots.default;
      // if (!s.length) {
      //   return false;
      // }
      // s = s[0];
      // console.log(s);
      // return (s.text.trim().length || s.children || s.tag) && true;
    }
  },
  methods: {
    replace(s) {
      return s.replace(/\$DEL/g, '<span class="redacted">hidden</span>');
    },
    toggle() {
      if (!this.hasMore) {
        return;
      }
      new window.bootstrap.Collapse(this.$refs.accordion);
    },
    openLink() {
      window.open(this.link);
    }
  }
};
</script>

<style lang="scss" scoped>
.dim {
  opacity: 0.8;
}
.more {
  white-space: pre-line;
}
.hashs {
  font-style: italic;
}
</style>
